.buttons-container button {
  padding: 8px;
  min-width: 80px;
  margin: 8px;
}

.info-container {
  background: black;
  color: wheat;
  padding: 8px;
  text-align: left;
  margin: 8px;
}

.info-container .links {
  background: white;
  margin-top: 16px;
  display: flex;
  justify-content: space-between;
  padding: 8px;
}

.cards-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.cards-container .card {
  width: 144px;
  box-shadow: 0px 1px 3px 1px rgb(195 189 189 / 33%);
  margin: 8px;
  padding: 8px;
}
